
<template>
	<view class="date">
		<!-- 周日到周六层 -->
		<view class=" df" style="flex-direction: column; margin: 0 63rpx; position: relative;">
			<view class="" style="">
				<view class="" @click="weekPre" style="position: absolute; top: 15rpx; left: -40rpx;">
					<image  src="../../static/mlestones/zuo.png" mode="widthFix" style="width: 40rpx;"></image>
				</view>
				<view class="" @click="weekNext" style="position: absolute; top: 15rpx; right: -40rpx;">
					<image  src="../../static/mlestones/you.png" mode="widthFix" style="width: 40rpx;"></image>
				</view>
				<view class="df-jcsb">
					<view class="ta-c fw800" 
					style="font-size: 15rpx; color: #225877; width: 60rpx;" 
					v-for="(item1,index1) in week" :key="index1">
					{{item1}}
					</view>
				</view>
				<view class="df-jcsb">
					<view class="ta-c" @click="pick(item)" 
					style="width: 60rpx; height: 50rpx; line-height: 50rpx; margin-top: 10rpx;" 
					v-for="(item,index) in days" :key="index">
						{{item.getDate()}}
					</view>
				</view> 
			</view>
			<view class="" style="height: 15rpx;"></view>
		</view>
	

	</view>
</template>

<script>
export default {
	data(){
		return {
			week:['SUN','MON','TUE','WEO','THU','FRI','SAT'],
			currentYear: 1970,   // 年份
			currentMonth: 1,  // 月份
			currentDay: 1,    // 日期
			currentWeek: 1,    // 星期
			days: [],
		}
	},

	methods:{
		formatDate (year, month, day) {
			const y = year
			let m = month
			if (m < 10) m = `0${m}`
			let d = day
			if (d < 10) d = `0${d}`
			return `${y}-${m}-${d}`
		}, 
		initData (cur) {
			let date = ''
			if (cur) {
				date = new Date(cur)
			} else {
				date = new Date()
			}
			this.currentDay = date.getDate()          // 今日日期 几号
			this.currentYear = date.getFullYear()       // 当前年份
			this.currentMonth = date.getMonth() + 1    // 当前月份
			this.currentWeek = date.getDay() // 1...6,0   // 星期几
			if (this.currentWeek === 0) {
				this.currentWeek = 7
			}
			const str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay)
			// 今日日期 年-月-日
			this.days.length = 0
			// 今天是周日，放在第一行第7个位置，前面6个 这里默认显示一周，如果需要显示一个月，则第二个循环为 i<= 35- this.currentWeek
			/* eslint-disabled */
			for (let i = this.currentWeek - 1; i >= 0; i -= 1) {
				const d = new Date(str)
				d.setDate(d.getDate() - i)
					// console.log(y:" + d.getDate())
				this.days.push(d)
			}
			for (let i = 1; i <= 7 - this.currentWeek; i += 1) {
				const d = new Date(str)
				d.setDate(d.getDate() + i)
				this.days.push(d)
			}
			console.log("1111");
			console.log(this.days);
		}, 	
		//  上个星期
		weekPre () {
			const d = this.days[0]    // 如果当期日期是7号或者小于7号
			d.setDate(d.getDate() - 7)
			this.initData(d)
		},
 
		//  下个星期
		weekNext () {
			const d = this.days[6]    // 如果当期日期是7号或者小于7号
			d.setDate(d.getDate() + 7)
			this.initData(d)
		},

		// 上一個月   传入当前年份和月份
		pickPre (year, month) {
			const d = new Date(this.formatDate(year, month, 1))
			d.setDate(0)
			this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1))
		},


		// 下一個月   传入当前年份和月份
		pickNext (year, month) {
			const d = new Date(this.formatDate(year, month, 1))
			d.setDate(35)
			this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1))
		}, 

		// 当前选择日期
		pick (date) {
			// console.log(date);
			let ss = this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate())
			console.log('ss=' + ss);
			// alert(this.formatDate(date.getFullYear(), date.getMonth() + 1, date.getDate()))
		},
		

	},
	created() {
		this.initData(null)
	}
}
</script>




